import React, { useState, useEffect } from "react";
import "../../css/bootstrap.min.css";
import axios from "axios";
import { FaBars, FaEnvelope, FaPowerOff } from "react-icons/fa"
export default function TopNavigation({ setLoggedIn }) {
  const [request, setRequest] = useState([]);
  useEffect(() => {
    axios.get("/api/partnerrequest").then((res) => {
      let requests = res.data.data;
      let pending = requests.filter((item) => item.status === "Pending");
      let numOfRequests = pending.length;
      res.data.data.length = 5;
      setRequest(numOfRequests);
    });
  }, []);
  const logout = () => {
    setLoggedIn(false);
    localStorage.setItem(
      "logged_in_token",
      JSON.stringify({
        logged_in: false,
      })
    );
  };

  return (
    <nav className="navbar-static-top pb-1 pt-3">
      <div className="nav-actions pb-2 d-lg-none d-xl-none">
        <FaBars size={18} color="#1872ab" />
      </div>
      <div className="d-lg-block d-xl-block"></div>
      <div className="top-nav-links">
        <div className="nav-actions mx-4">
          <div className="partner-dropdown">
            <span className="fa-stack fa-1x" data-count={request}>
              <FaEnvelope size={18} color="#1872ab" />
            </span>
            <div className="partner-dropdown-content">
              hello
              {/* <li>
              <a className="dropdown-item" href="#">Some news</a>
            </li>
            <li>
              <a className="dropdown-item" href="#">Another news</a>
            </li>
            <li>
              <a className="dropdown-item" href="#">Something else here</a>
            </li> */}
            </div>
          </div>

        </div>
        <div className="nav-actions pb-2">
          <a href="/" onClick={logout} data-tooltip="Logout" className="d-flex align-items-center">
            <FaPowerOff size={18} />
            <span className="log-out"> Log out</span>
          </a>
        </div>
      </div>
    </nav>
  );
}
